@font-face {
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff") format("woff"), url("../fonts/oswaldmedium.woff2") format("woff2");
}
body {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  line-height: 1;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

a {
  text-decoration: none;
  color: #000000;
}

button {
  border: 0;
  padding: 0;
}

ol,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: none;
}

form,
fieldset {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.additional-products {
  box-sizing: content-box;
  text-transform: none;
  padding: 25px 0 20px 0;
  background: linear-gradient(to bottom, transparent 0, transparent 37px, #68b738 37px, #68b738 40px, transparent 40px, transparent 100%);
}
@media (min-width: 768px) {
  .additional-products {
    background: linear-gradient(to bottom, transparent 0, transparent 90px, #68b738 90px, #68b738 93px, transparent 93px, transparent 100%);
  }
}
@media (min-width: 768px) {
  .additional-products {
    padding: 70px 30px;
  }
}
@media (min-width: 1440px) {
  .additional-products {
    padding: 75px 110px 85px 70px;
  }
}
@media (min-width: 1440px) {
