.example__toggler-scrollbar {
  text-transform: uppercase;
  display: flex;
  margin-bottom: 20px;
  min-width: 210px;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
  line-height: 20px;
}
.example__scrollbar--desc {
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  border-bottom: 1px dotted #000000;
}
@media (min-width: 768px) {
  .example__scrollbar--desc {
    font-size: 20px;
    line-height: 30px;
  }
}
.example__scrollbar--desc:hover {
  border-bottom: 1px solid;
}
.example__scrollbar--desc:active {
  border-bottom: none;
}
.example__scrollbar--input {
  --color: linear-gradient(
    to right,
    #68b738 0%,
    #68b738 50%,
    #ffffff 50%,
    #ffffff 100%
  );
  display: block;
  position: relative;
  appearance: none;
  width: 85px;
  background: #ffffff;
  border-radius: 6px;
  padding: 4px;
}
@media (min-width: 768px) {
  .example__scrollbar--input {
    background: #dcdcdc;
    width: 425px;
    height: 6px;
    border: 1px solid transparent;
    border-radius: 6px;
  }
}
.example__scrollbar--input::-ms-track {
  display: block;
  cursor: pointer;
  height: 6px;
  border-radius: 6px;
  background: var(--color);
}
@media (min-width: 768px) {
  .example__scrollbar--input::-ms-track {
    height: 6px;
    background: #dcdcdc;
  }
}
.example__scrollbar--input::-webkit-slider-runnable-track {
  display: block;
  cursor: pointer;
  height: 6px;
  border-radius: 6px;
  background: var(--color);
}
@media (min-width: 768px) {
  .example__scrollbar--input::-webkit-slider-runnable-track {
    height: 6px;
    background: #dcdcdc;
  }
}
.example__scrollbar--input::-moz-range-track {
  display: block;
  cursor: pointer;
  height: 6px;
  border-radius: 6px;
  background: var(--color);
}
@media (min-width: 768px) {
  .example__scrollbar--input::-moz-range-track {
    height: 6px;
    background: #dcdcdc;
  }
}
.example__scrollbar--input::-webkit-slider-thumb {
  padding: 0;
  margin-top: -14px;
  appearance: none;
  cursor: pointer;
  box-sizing: border-box;
  width: 5px;
  height: 5px;
}
@media (min-width: 768px) {
  .example__scrollbar--input::-webkit-slider-thumb {
    width: 35px;
    height: 35px;
    box-shadow: 0 1px 1px #cdcdcd;
    border: 1px solid #cdcdcd;
    border-radius: 17px;
    background: radial-gradient(circle at 50%, #68b738 20%, #ffffff 1%);
  }
}
.example__scrollbar--input::-webkit-slider-thumb:active {
  border: 1px solid #68b738;
}
.example__scrollbar--input::-moz-range-thumb {
  padding: 0;
  margin-top: -14px;
  appearance: none;
  cursor: pointer;
  box-sizing: border-box;
  width: 5px;
  height: 5px;
}
@media (min-width: 768px) {
  .example__scrollbar--input::-moz-range-thumb {
    width: 35px;
    height: 35px;
    box-shadow: 0 1px 1px #cdcdcd;
    border: 1px solid #cdcdcd;
    border-radius: 17px;
    background: radial-gradient(circle at 50%, #68b738 20%, #ffffff 1%);
  }
}
.example__scrollbar--input::-moz-range-thumb:active {
