  opacity: 0.3;
}
.programms-desc-item__footer::before {
  cursor: pointer;
  content: "";
  display: inline-block;
  position: absolute;
  left: 110px;
  top: 8px;
  width: 18px;
  height: 2px;
  background: #000000;
  animation-duration: 4s;
}
@media (min-width: 767px) {
  .programms-desc-item__footer::before {
    top: 16px;
    left: 120px;
  }
}
.programms-desc-item__footer::after {
  cursor: pointer;
  content: "";
  position: absolute;
  left: 128px;
  top: 4px;
  border: 5px solid #000000;
  border-left-width: 7px;
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-right-color: transparent;
}
@media (min-width: 768px) {
  .programms-desc-item__footer::after {
    top: 12px;
    left: 138px;
  }
}
.programms-desc-item__footer:hover::before {
  animation: grown 1s ease-in-out;
}
.programms-desc-item__footer:hover::after {
  animation: move-horizontally 1s ease-in-out;
}
@media (min-width: 768px) {
  .programms-desc-item__footer:hover::after {
    animation: move-horizontally-768 1s ease-in-out;
  }
}
@keyframes grown {
  from {
    width: 18px;
  }
  to {
    width: 26px;
  }
}
@keyframes move-horizontally {
  from {
    left: 128px;
  }
  to {
    left: 136px;
  }
}
@keyframes move-horizontally-768 {
  from {
    left: 138px;
  }
  to {
    left: 146px;
  }
}

